body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke;
  font: #232b2b;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.formDiv{
    position: relative;
    margin: auto;
    display: block;
    margin-top: 10px;
    border: 1px solid darkgray;
    width: 50%;
    text-align: center;
    padding: 50px;
    padding-top: 10px;
    border-radius: 8px;
    background-color: white;
}

.formDiv>p{
    font-size: 30px;
}
.inputText{
    width: 100%;
    padding: 10px;
    border: 1px solid black;
}

.submitButton{
    width: 103%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid black;
    background-color: lightblue;
    cursor: pointer;
}

.grid-container {
    /* margin-left: 30px; */
    display: grid;
    align-content: start;
    grid-template-columns: auto auto auto;
    grid-gap: 30px;
    padding: 30px;
    margin-left: 10%;
  }

  div.polaroid {
    width: 300px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
    cursor: pointer;
  }
  
  div.container {
    padding: 10px;
  }
  /* .grid-container img{
    width: 15em;
    height: 15em;
    border-radius: 8px;
  } */

  .grid-container > div:hover{
    box-shadow: 10px 10px 5px grey;
    
  }
.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0,0,0, 0.5);
  }
  .popup_inner {
    position: absolute;
    left: 35%;
    right: 35%;
    top: 35%;
    bottom: 35%;
    margin: auto;
    background: white;
    border: 1px solid darkgray;
    border-radius: 3px;
  }

  .content{
    text-align: center;
    font: 20px;
  }

  .content button{
      text-align: center;
      background-color: lightgray;
      border: 1px solid darkgray;
      padding: 8px;
      border-radius: 5px;
      cursor: pointer;
  }
  .content button:hover{
    background-color: lightblue;
  }
